<template>
  <yd-layout id="conlist" >
    <yd-navbar slot="navbar" bgcolor="#0FA0FA" color="#fff">
        <yd-navbar-back-icon color="#fff"  @click.native="goback" slot="left"></yd-navbar-back-icon>
        <div slot="center">
             <span class="tab" :class="{ active: isActive }" @click="changetype('list')">列表</span><span  class="tab"  :class="{ active: !isActive }" @click="changetype('chart')">图表</span>
        </div>
    </yd-navbar>
    <div v-show="isActive" class="list">
         <div v-for="(item,n) in companyList" class="companylist" @click="goItem(item.cEconomicsId,item.cEconomicsName)">
           <div style="border-bottom: solid 1px #eee">
             <yd-cell-item>
               <span slot="left">{{item.cEconomicsName}}</span>
               <span slot="right">{{item.countCompany}}个企业</span>
             </yd-cell-item>
           </div>
         </div>
   </div>
    <div v-show="!isActive" class="baicontent" >
       <div class="lineChart">
         <div class="center">
           <div class="line" v-for="(item,n) in companyList" :style="'transform: rotate('+parseInt(360/(companyList.length))*n+'deg)' " @click="goItem(item.cEconomicsId,item.cEconomicsName)">
              <div class="lineItem" v-for="i in item.levelList" >
                <span class="lineleft"></span>
                <span class="linecenter"></span>
                <span class="lineright"></span>
              </div>
             <div class="centerLine"></div>
             <span class="lineName" :style="'transform: rotate('+(-parseInt(360/(companyList.length))*n)+'deg)' ">{{item.cEconomicsName}}</span>
           </div>

         </div>
       </div>
    </div>



  </yd-layout>
</template>

<script>
  let echarts = require('echarts/lib/echarts')
  import { mapMutations,mapState } from 'vuex'
export default {

  data () {
    return {

      centerId:"",
      companyList:[],
    }
  },
  watch:{

  },
  computed:{
    ...mapState({
      isActive:"isActive"
    })
  },
  mounted: function(){
    this.centerId = this.$route.query.centerId;
    this.getList();

//    this.initchart();
  },

  methods:{
    ...mapMutations([
      'setisActive',
    ]),
    goback:function () {
      this.$router.go(-1)
    },
    changetype:function (type) {
      if(type=='list'){
        this.setisActive(true)
      }
      if(type=='chart'){
        this.setisActive(false)
      }

    },
    getList:function (page) {
      let vm = this;
      vm.api(vm,'post',"信息获取中...","common/company/findEconomicsAndCompany",{
        pointCenterId:vm.centerId,
      },function (data) {
        console.log(data);
        if(data.statusCode == 0){
          vm.companyList = vm.companyList.concat(data.dataInfo.listData);
        }
      });
    },
    goItem:function (cEconomicsId,cEconomicsName) {
        console.log(cEconomicsId);
        let vm = this;
        this.$router.push({path:"ectypeList",query:{
          cEconomicsId:cEconomicsId,
          pointCenterId:vm.centerId,
          cEconomicsName:cEconomicsName
        }})

    }

  }
}
</script>

<style scoped>
  #conlist{
    width: 100%;
    height: 100%;
  }
  .tab{
    display: inline-block;
    line-height: 30px;
    text-align: center;
    font-size: 14px;
    height: 30px;
    width: 80px;
    background-color: #fff;
    color: #0076FF;

  }
.list{
  width: 100%;
  height: 100%;
  background-color: #fff;
}
  .active{
    background-color: #222;
    box-shadow: 0 1px 5px #333 , 0 -1px 5px #333;
    color: #fff;
    transition: all .5s ;
  }
  .baicontent{
  width: 100%;
    height:100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .baicontent::before{
    content: "";
    position: absolute;
    background-image: url("../../../assets/baitu.png");
    filter:blur(2px);
    z-index: -1;
    width: 100%;
    height: 100%;
    top: 0px;
    left: 0px;
    background-size: cover;
    overflow:hidden;
  }
  .lineChart{
    width: 91vw;
    height: 91vw;
    border: solid 1px #186EF9;
    display: flex;
    border-radius: 50%;
    justify-content: center;
    align-items: center;
    opacity: 1;
  }
  .line{
    position: absolute;
    margin-top: -30px;
    width: 45vw;
    height: 60px;
    transform-origin: left;
    display: flex;
    align-items: center;
  }
  .line:hover .lineItem .linecenter{
     background-color: #05a;
     box-shadow: 0 1px 5px #aaa;
  }
  .line:hover  .centerLine{
    border-bottom: dashed 2px #555;
  }
  .centerLine{
    position: absolute;
    width: 100%;
    border-bottom: dashed 1px #999;
  }
  .lineItem:first-child{
    margin-left: 10px;
  }

  .lineName{
     position: absolute;
     bottom:-5px;
     right:0px;
    transform-origin: center;
  }
  .lineItem{
    flex: 1;
    height: 10px;
    display: flex;
  }
  .lineleft{
    flex: 2;
    /*background-color: #eee;*/
  }
  .linecenter{
    flex: 5;
    max-width: 35px;
    background-color: #10aeff;
    z-index: 999;
  }
  .lineright{
    flex: 1;
  }

</style>



